@charset "UTF-8";

body {
  margin: 0;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
}

.flex-wrap {
  display: flex;
  justify-content: space-between;
}

.video {
  width: 450px;

  iframe {
    width: 100%;
    height: 300px;
  }

  .video__media {
    width: 100%;
    height: 300px;
    border: none;
  }

  .view-block-wrap {
    position: relative;

    .video__link {
      display: block;
      width: 100%;
      height: 100%;
    }

    .video__button {
      cursor: pointer;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 61px;
      height: 61px;
      border-radius: 50%;
      border: none;
      padding: 0;
      background-image: url("data:image/svg+xml;base64,");
      transform: translate(-50%, -50%);
    }
  }
}
